import axios from "axios";
export default {
  name: "Song",
  data() {
    return {
      urlId: "",
      urlImg: "",
      lrcArr: [], //歌词
      audioSound: "", //声音播放地址
      isTurn: false //是否转动
    };
  },
  methods: {
    //获取歌曲mp3地址
    getSongUrl() {
      //注 : 部分用户反馈获取的 url 会 403,hwaphon找到的 解决方案是当获取到音乐的 id 后，
      //将 https://music.163.com/song/media/outer/url?id=id.mp3 以 src 赋予 Audio 即可播放
      let url = "http://musicapi.leanapp.cn/song/url?id=33894312"; //获取音乐 url
      axios.get(url).then(res => {
        // console.log("getSong:", res);
      });
    },
    //获取歌词
    getSongLrc() {
      let url = "http://musicapi.leanapp.cn/lyric?id=" + this.urlId;
      axios.get(url).then(res => {
        // console.log("歌词:", res);
        //this.lrcArr=res.data.lrc.lyric;
        // console.log("lrcArr:", res.data.lrc.lyric);
      });
    },
    playSong() {
      //自动播放音乐
      var url =
        "https://music.163.com/song/media/outer/url?id=" + this.urlId + ".mp3";
      this.audioSound = url;
      this.isTurn = true;
    },
    //获取歌曲详情
    getSoundDetail() {
      // http://api.javaswing.cn/song/detail?ids=1336856777
      let url = "http://musicapi.leanapp.cn/song/detail?ids=" + this.urlId; //获取音乐 url
      axios.get(url).then(res => {
        //console.log("歌曲详情:", res.data.songs[0].al.picUrl);
        this.urlImg = res.data.songs[0].al.picUrl;
      });
    },
    //音乐播放
    onSoundPlay() {
      this.isTurn = !this.isTurn;
      //console.log("状态:",this.$refs.audio.paused)
      //true 指示音频/视频已暂停
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
    }
  },
  mounted() {
    this.urlId = this.$route.params.id;
    this.getSongLrc(); //歌词
    this.playSong(); //自动播放音乐
    this.getSoundDetail(); //歌曲详情(获取图片)
  }
};
